<template>
  <div class="row">
    <div class="col-sm-12 col-md-6">
      <div
        id="simple-table_length"
        class="dataTables_length"
      >
        <label>Отображено
          <select
            v-model.number="limit"
            name="simple-table_length"
            aria-controls="simple-table"
            class="form-control form-control-sm"
          >
            <option value="10">10</option>
            <option value="25">25</option>
            <option value="50">50</option>
            <option value="100">100</option>
          </select>
          записей на страницу
        </label>
      </div>
    </div>
    <div class="col-sm-12 col-md-6">
      <div
        id="simple-table_filter"
        class="dataTables_filter"
      >
        <label>Поиск:<input
          type="search"
          class="form-control form-control-sm"
          placeholder=""
          aria-controls="simple-table"
        ></label>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations } from 'vuex';

export default {
  name: 'BillChildFilter',
  computed: {
    limit: {
      get() {
        return this.$store.state.bill.limit;
      },
      set(newValue) {
        this.setLimit(newValue);
        this.setPage(1);
        this.getPaymentRequests();
      },
    },
  },
  methods: {
    ...mapMutations('bill', ['setLimit', 'setPage']),
    ...mapActions('bill', ['getPaymentRequests']),
  },
};
</script>
