<script>
import { mapState, mapMutations, mapActions } from 'vuex';

import Header from '@/components/Header';
import Footer from '@/components/Footer';
import Breadcrumbs from '@/components/Breadcrumbs';
import BillChildFilter from '@/components/bill_child/BillChildFilter';
import BillChildTable from '@/components/bill_child/BillChildTable';
import BillChildAction from '../components/bill_child/BillChildAction';

export default {
  components: {
    BillChildAction,
    BillChildTable,
    BillChildFilter,
    Breadcrumbs,
    Header,
    Footer,
  },
  data: () => ({
    pages: [
      {
        title: 'Ведомости',
        url: '/bill',
      },
    ],
  }),
  computed: {
    ...mapState('bill', ['bill']),
  },
  mounted() {
    this.reset();
    this.setBillId(this.$route.params.id);
    this.getBill().then(() => {
      this.pages.push({
        title: `Платежная ведомость #${this.bill.key}`,
      });
    });
    this.getPaymentRequests();
  },
  methods: {
    ...mapMutations('bill', ['setBillId']),
    ...mapActions('bill', ['getBill', 'reset', 'getPaymentRequests']),
  },
};
</script>
