<template>
  <div class="row">
    <div
      class="col-sm-12"
      :class="{
        'col-md-5': !selectedPaymentRequest.length,
        'col-md-3': selectedPaymentRequest.length,
      }"
    >
      <div
        id="simple-table_info"
        class="dataTables_info"
        role="status"
        aria-live="polite"
      >
        Показано с {{ fullCount }} по {{ firstRecord }} из {{ lastRecord }} записей
      </div>
    </div>
    <div
      v-if="selectedPaymentRequest.length"
      class="col-sm-12 col-md-6 row align-items-center"
    >
      <div class="col-md-4">
        Действия с выбранными
      </div>
      <div class="col-md-4">
        <select
          v-model="selectAction"
          class="form-control custom-select"
          data-placeholder="Выберите"
        >
          <option
            disabled
            selected
            hidden
            :value="null"
          >
            Выберите
          </option>
          <option :value="1">
            Оплатить
          </option>
          <option :value="2">
            Вернуть в заявки
          </option>
        </select>
      </div>
      <div class="col-md-4">
        <button
          class="btn btn-primary"
          @click="action"
        >
          Выполнить
        </button>
      </div>
    </div>
    <div
      class="col-sm-12"
      :class="{
        'col-md-7': !selectedPaymentRequest.length,
        'col-md-3': selectedPaymentRequest.length,
      }"
    >
      <div
        id="simple-table_paginate"
        class="dataTables_paginate paging_simple_numbers"
      >
        <paginate
          v-if="totalPages > 1"
          v-model="page"
          :page-count="totalPages"
          prev-text="Пред."
          next-text="След."
          container-class="pagination"
          page-class="paginate_button page-item"
          page-link-class="page-link"
          prev-link-class="page-link"
          next-link-class="page-link"
          prev-class="paginate_button page-item previous"
          next-class="paginate_button page-item next"
          :hide-prev-next="true"
        />
      </div>
    </div>
  </div>
</template>

<script>
import {
  mapActions, mapGetters, mapMutations, mapState,
} from 'vuex';

import axios from '@/service/axios';
import qs from 'qs';

export default {
  name: 'BillChildAction',
  data: () => ({
    selectAction: null,
  }),
  computed: {
    page: {
      get() {
        return this.$store.state.bill.page;
      },
      set(newValue) {
        this.setPage(newValue);
        this.getPaymentRequests();
      },
    },
    ...mapState('bill', ['fullCount', 'totalPages', 'selectedPaymentRequest']),
    ...mapGetters('bill', ['firstRecord', 'lastRecord']),
  },
  methods: {
    async payPaymentRequest() {
      const { data } = await axios.post(
        '/pay-payment-request/',
        qs.stringify(
          { payment_requests: this.selectedPaymentRequest },
          { arrayFormat: 'repeat', encode: false },
        ),
      );
      if (data.status === true) {
        // TODO: add notification
        this.setSelectedPaymentRequest([]);
        this.getPaymentRequests();
      }
    },
    async backPaymentRequest() {
      const { data } = await axios.post(
        '/back-payment-request/',
        qs.stringify(
          { payment_requests: this.selectedPaymentRequest },
          { arrayFormat: 'repeat', encode: false },
        ),
      );
      if (data.status === true) {
        // TODO: add notification
        this.setSelectedPaymentRequest([]);
        this.getPaymentRequests();
      }
    },
    action() {
      if (this.selectAction === 1) {
        return this.payPaymentRequest();
      }
      if (this.selectAction === 2) {
        return this.backPaymentRequest();
      }
      return null;
    },
    ...mapMutations('bill', ['setPage', 'setSelectedPaymentRequest']),
    ...mapActions('bill', ['getPaymentRequests']),
  },
};
</script>
